.name{
  display:inline;
  padding: 10px;
  font-family: 'Fjalla One', sans-serif;
  color: black;
  font-size: 4rem;
}

.cardTitle{
  font-family: 'Fjalla One', sans-serif;
  font-size: 4rem;
}

.cardBody{
  font-family: 'Fjalla One', sans-serif;
  font-size: 2rem;
}

.postCard{
  font-family: 'Special Elite', cursive;
  font-size: 30px;
  text-align: "center";
  padding-top: 10px;
}

.card{
  width: auto;
  background-color:rgba(255,255,255,0.8);
  padding:20px;
  border-radius:15px;
}

.tetriscard{
  width: 16rem;
  height: auto;
  background-color:rgba(255,255,255,0.8);
  padding:20px;
  border-radius:15px;
}

.container {
  position: relative;
}


.container .next,
.prev {
  position: absolute;
  top: 50%;
  background-color: #555;
  color: white;
  font-size: 16px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.next {
  right:10px;
}

.prev {
  left:10px;
  transform: scale(-1);
}